@mixin gradient-v($from, $to) {
	background: $from;
	background: -webkit-linear-gradient(top, $from 0%, $to 100%);
	background:    -moz-linear-gradient(top, $from 0%, $to 100%);
	background:     -ms-linear-gradient(top, $from 0%, $to 100%);
	background:      -o-linear-gradient(top, $from 0%, $to 100%);
	background:         linear-gradient(top, $from 0%, $to 100%);
}

@mixin gradient-v-half-bottom($from, $to) {
	background: $from;
	background: -webkit-linear-gradient(top, $from 0%, $from 50%, $to 100%);
	background:    -moz-linear-gradient(top, $from 0%, $from 50%, $to 100%);
	background:     -ms-linear-gradient(top, $from 0%, $from 50%, $to 100%);
	background:      -o-linear-gradient(top, $from 0%, $from 50%, $to 100%);
	background:         linear-gradient(top, $from 0%, $from 50%, $to 100%);
}

@mixin vendor($key, $value) {
	-webkit-#{$key}: $value;
	   -moz-#{$key}: $value;
	    -ms-#{$key}: $value;
	     -o-#{$key}: $value;
	        #{$key}: $value;
}

// TODO: Use "vendor" everywhere

@mixin transition($value) {
	-webkit-transition: $value;
	   -moz-transition: $value;
	     -o-transition: $value;
	        transition: $value;
}

@mixin animation($value) {
	-webkit-animation: $value;
	   -moz-animation: $value;
	     -o-animation: $value;
	        animation: $value;
}

@mixin animation-prop($prop, $value) {
	-webkit-animation-#{$prop}: $value;
	   -moz-animation-#{$prop}: $value;
	     -o-animation-#{$prop}: $value;
	        animation-#{$prop}: $value;
}

@mixin keyframes($name) {
	@-webkit-keyframes #{$name} {
		@content
	}

	@-moz-keyframes #{$name} {
		@content
	}

	@-o-keyframes #{$name} {
		@content
	}

	@keyframes #{$name} {
		@content
	}
}
