BUTTON,
INPUT {
	outline: none;
	white-space: nowrap;
}


/**
* Button states
*/

$state:      nth($interact, 1);
$first-c1:   nth($state, 2);
$first-c2:   nth($state, 3);
$first-text: nth($state, 4);

BUTTON,
INPUT[type="button"],
INPUT[type="submit"],
.su-current {
	@include gradient-v-half-bottom(#{$first-c1}, #{$first-c2});

	margin: 0;
	padding: 1px 15px 1px;

	outline: none;
	border: 1px solid darken($first-c2, 10%);
	color: #{$first-text};

	border-radius: 2px;

	text-shadow: 0 1px 0 lighten($first-text, 100%);
	font-size: 14px;

	line-height: 29px;

	.su-icon {
		margin-right: 5px;
	}

	&.su-secondary {
		background: $c1;
		color: $c6;
		text-shadow: none;
		border-color: $c6;
	}

	&.su-hover, &:hover {
		color: #{$first-text};
		@include gradient-v($first-c1, $first-c2);
	}

	&.su-active, &:active {
		background: darken($first-c1, 2%);
		box-shadow: inset 3px 3px 5px -2px darken($first-c2, 10%);
		padding: 2px 15px 0;
	}

	&.su-focus, &:focus {
		border-color: darken($first-c2, 30%);
	}

	&.su-loading {
		background-color: #{$first-c1};
		background-image: url(data:image/png;base64,) !important;
		background-position: 0 0;
		@include animation(loading 750ms linear infinite);
		box-shadow: inset 3px 3px 5px -2px darken($first-c2, 10%);
		padding: 1px 15px 1px;
	}

	&.su-loading.su-focus, &.su-loading:focus {
		border-color: darken($first-c2, 10%);
	}

	&:disabled {
		background: $c5 !important;
		box-shadow: inset 0 2px 4px -2px $c2 !important;
		color: $c1;
		text-shadow: 0 -1px 0 darken($c1, 25%) !important;
		border: 1px solid darken($c5, 10%) !important;
		padding: 1px 15px 1px;
	}
}


/**
* Sizes
*/

BUTTON,
INPUT[type="button"],
INPUT[type="submit"] {
	&.su-big {
		font-size: 16px;
		padding-left: 20px;
		padding-right: 20px;
		line-height: 36px;
	}
}


/**
* Other colors
*/

@for $i from 2 through length($interact) {
	$state:     nth($interact, $i);
	$this:      unquote(nth($state, 1));
	$this-c1:   nth($state, 2);
	$this-c2:   nth($state, 3);
	$this-text: nth($state, 4);

	BUTTON.su-#{$this},
	INPUT[type="button"].su-#{$this},
	INPUT[type="submit"].su-#{$this},
	.su-current.su-#{$this} {
		@include gradient-v($this-c1, $this-c2);
		border: 1px solid darken($this-c1, 8%);
		color: $this-text;
		text-shadow: 0 1px 0 darken($this-text, 50%);
		box-shadow: 0 1px 2px transparentize($c0, .7);

		&.su-secondary {
			background: $c1;
			color: $c6;
			text-shadow: none;
			border-color: $c6;
			box-shadow: none;
		}

		&.su-hover, &:hover {
			color: $this-text;
			border-color: darken($this-c1, 8%);
			box-shadow: 0 1px 2px transparentize($c0, .7);
			@include gradient-v(darken($this-c1, 2%), darken($this-c2, 2%));
		}

		&.su-active, &:active {
			background: $this-c2;
			box-shadow: inset 3px 3px 5px -2px darken($this-c2, 10%);
			text-shadow: none;
		}

		&.su-focus, &:focus {
			border-color: darken($this-c2, 15%);
		}

		&.su-loading {
			background-color: mix($this-c1, $this-c2);
			box-shadow: inset 3px 3px 5px -2px darken($this-c2, 10%);
			text-shadow: none;
		}

		&.su-loading.su-focus, &.su-loading:focus {
			border: 1px solid darken($this-c1, 5%);
		}
	}
}


/**
* Circle button
*/

BUTTON.su-circle {
	background: transparent;
	border: none;
	padding: 0;
	color: $c0;
	box-shadow: none;
	font-size: 17px;
	font-weight: bold;
	line-height: 50px;

	.su-icon {
		@include gradient-v-half-bottom($first-c1, $first-c2);
		display: inline-block;
		vertical-align: middle;
		width: 50px;
		height: 48px;
		padding: 1px 0;
		border-radius: 50px;
		border: 1px solid lighten($c7, 40%);
		font-size: 30px;
		margin-right: 10px;
		line-height: 48px;
	}

	.su-text {
		display: inline-block;
		vertical-align: middle;
		padding: 1px 0;
	}

	&:hover {
		.su-icon {
			@include gradient-v($first-c1, $first-c2);
		}
	}

	&.su-focus, &:focus {
		.su-icon {
			border-color: darken($first-c2, 30%);
		}
	}

	&.su-active, &:active {
		.su-icon {
			background: darken($first-c1, 2%);
			box-shadow: inset 3px 3px 5px -2px darken($first-c2, 10%);
		}

		.su-icon, .su-text {
			padding: 2px 0 0;
		}
	}

	&:disabled {
		background: none !important;
		border: none !important;

		.su-icon {
			background: $first-c2 !important;
			box-shadow: none;
		}

		.su-icon, .su-text {
			color: lighten($first-text, 15%);
			text-shadow: none;
			padding: 1px 0 !important;
		}
	}
}


/**
* Group
*/

.su-button-group {
	BUTTON,
	INPUT[type="button"],
	INPUT[type="submit"] {
		float: left;
		border-radius: 0;

		&:first-child {
			border-radius: 2px 0 0 2px;
		}

		&:last-child {
			border-radius: 0 2px 2px 0;
		}

		&:not(:first-child) {
			border-left: none;
		}

		&.su-focus, &:focus {
			border-color: $c6;
		}
	}
}


/**
* Animation
*/

@include keyframes(loading) {
	0%   { background-position: 0 0; }
	100% { background-position: 20px 0; }
}
